import { Observable, fromEvent, merge } from "rxjs";

const StatsEvent = {
    SELECT_ALL_CUISINES: new CustomEvent("click", {
        detail: {
            selectAll: true,
            section: 'cuisines'
        }
    }),
    SELECT_ALL_LOCALES: new CustomEvent("click", {
        detail: {
            selectAll: true,
            section: 'locales'
        }
    }),
    SELECT_NONE_CUISINES: new CustomEvent("click", {
        detail: {
            selectNone: true,
            section: 'cuisines'
        }
    }),
    SELECT_NONE_LOCALES: new CustomEvent("click", {
        detail: {
            selectNone: true,
            section: 'locales'
        }
    }),
};

class Stats {
    private statsTitles: NodeListOf<HTMLDivElement>;
    private statsLevels: NodeListOf<HTMLDivElement>;
    private localeFilterButtons: NodeListOf<HTMLDivElement>;
    private cuisineFilterButtons: NodeListOf<HTMLDivElement>;

    private localeFilterButtons$ = new Observable<Event>();
    private cuisineFilterButtons$ = new Observable<Event>();
    private toggleFilterButtons$ = new Observable<Event>();

    private firstLevelMap: Record<string, HTMLElement> = {};
    private secondLevelMap: Record<string, Array<HTMLElement>> = {};

    private toggleFiltersButton: HTMLDivElement;

    init() {
        this.toggleFiltersButton = document.querySelector('#filters');

        this.statsTitles = document.querySelectorAll('#content .stats .title');
        this.statsTitles.forEach((element: HTMLElement) => {
            element.onclick = (e) => this.handleStatsClick(e);
        });

        //#region toggle filter buttons
        const toggleFilterButtons = document.querySelectorAll('.filter-button');
        toggleFilterButtons.forEach((element: HTMLElement) => {
            const clickEvent = fromEvent(element, 'click');
            this.toggleFilterButtons$ = merge(this.toggleFilterButtons$, clickEvent);
        });
        
        this.toggleFilterButtons$.subscribe((e) => this.handleToggleFilterClick(e));
        //#endregion

        //#region actual filter buttons
        this.localeFilterButtons = document.querySelectorAll('.locale-filters button');
        this.localeFilterButtons.forEach((element: HTMLElement) => {
            const clickEvent$ = fromEvent(element, 'click');
            this.localeFilterButtons$ = merge(this.localeFilterButtons$, clickEvent$);

            // HACK:
            element.classList.add('active');

            // TODO: set up localStorage to auto-set "active" state
        });
        this.localeFilterButtons$.subscribe((e) => this.handleFilterClick(e));

        this.cuisineFilterButtons = document.querySelectorAll('.cuisine-filters button');
        this.cuisineFilterButtons.forEach((element: HTMLElement) => {
            const clickEvent$ = fromEvent(element, 'click');
            this.cuisineFilterButtons$ = merge(this.cuisineFilterButtons$, clickEvent$);

            // HACK:
            element.classList.add('active');

            // TODO: set up localStorage to auto-set "active" state
        });

        this.cuisineFilterButtons$.subscribe((e) => this.handleFilterClick(e));
        //#endregion

        //#region set up first level map
        const firstLevelNodes = document.querySelectorAll('.first-level');
        firstLevelNodes.forEach((elem: HTMLElement) => {
            if (elem.dataset.cuisine) {
                this.firstLevelMap[elem.dataset.cuisine] = elem;
            }
        });

        const secondLevelNodes = document.querySelectorAll('.second-level a');
        secondLevelNodes.forEach((elem: HTMLElement) => {
            if (elem.dataset.locale) {
                if (this.secondLevelMap[elem.dataset.locale] instanceof Array) {
                    this.secondLevelMap[elem.dataset.locale].push(elem);
                } else {
                    this.secondLevelMap[elem.dataset.locale] = [elem];
                }
            }
        });
        //#endregion

        this.statsLevels = document.querySelectorAll('.first-level, .second-level, .third-level, .toggle-level');
        this.statsLevels.forEach((element: HTMLElement) => {
            element.onclick = (e) => this.handleStatsClick(e);
        });
    }

    private handleToggleFilterClick(e: Event) {
        e.stopPropagation();
        const target = e.target as HTMLElement;

        switch(target.dataset.action) {
            case 'toggle-filter':
                target.classList.toggle('active');
                this.toggleFiltersButton.classList.toggle('hide');
                target.textContent = this.toggleFiltersButton.classList.contains('hide') ? 'Show Filters' : 'Hide Filters';
                break;
            case 'select-all':
                if (target.dataset.section === 'cuisines') {
                    this.cuisineFilterButtons.forEach((event) => event.dispatchEvent(StatsEvent.SELECT_ALL_CUISINES));
                } else if (target.dataset.section === 'locales') {
                    this.localeFilterButtons.forEach((event) => event.dispatchEvent(StatsEvent.SELECT_ALL_LOCALES));
                }
                break;
            case 'select-none':
                if (target.dataset.section === 'cuisines') {
                    this.cuisineFilterButtons.forEach((event) => event.dispatchEvent(StatsEvent.SELECT_NONE_CUISINES));
                } else if (target.dataset.section === 'locales') {
                    this.localeFilterButtons.forEach((event) => event.dispatchEvent(StatsEvent.SELECT_NONE_LOCALES));
                }
                break;
        }
    }

    private handleFilterClick(e: Event) {
        const target = e.target as HTMLElement;

        const isCustomEvent = e instanceof CustomEvent;
    
        if (isCustomEvent) {
            if (e.detail.section === target.dataset.section) {
                if (e.detail.selectAll && !target.classList.contains('active')) {
                    target.classList.toggle('active');
                } else if (e.detail.selectNone && target.classList.contains('active')) {
                    target.classList.toggle('active');
                }
            }
        } else {
            target.classList.toggle('active');
        }

        // Tag and LocaleTag exist only on the direct button presses
        if (target.dataset.tag) { // cuisine tags
            const relatedItem = this.firstLevelMap[target.dataset.tag];
            if (target.classList.contains('active')) {
                relatedItem.classList.remove('hide');
            } else if (!target.classList.contains('active')) {
                relatedItem.classList.add('hide');
            }
        } else if (target.dataset.localeTag) { // e.g. lax, nyc, etc
            const relatedItems = this.secondLevelMap[target.dataset.localeTag];

            if (relatedItems instanceof Array) {
                relatedItems.forEach((item) => {
                    const isItemHidden = item.classList.contains('hide');

                    if (isCustomEvent) {
                        if (e.detail.selectNone === isItemHidden 
                            || e.detail.selectAll === !isItemHidden) 
                        {
                            return;
                        }
                    }

                    if (isItemHidden) {
                        item.classList.remove('hide');
                    } else if (!isItemHidden) {
                        item.classList.add('hide');
                    }
                });
            }
        }
    }

    private handleStatsClick(e: Event) {
        // console.log('got here', e);

        // const domNode = e.target as HTMLDivElement;
        
        // if (domNode) {
        //     const text = domNode.textContent.toLowerCase();
        //     switch (text) {
        //         case 'cuisine breakdown':
        //             console.warn('[Stats] cuisine breakdown');
        //             break;

        //         case 'restaurant breakdown':
        //             console.warn('[Stats] restaurant breakdown');
        //             break;

        //         case 'locales':
        //             console.warn('[Stats] Locales filters');
        //             break;

        //         case 'cuisines':
        //             console.warn('[Stats] Cuisines filters');
        //             break;
                
        //         default:
        //             console.warn('[Stats] dom', text);
        //     }
        // }
    }
}

export default new Stats();