import Menu from './menu';
import Stats from './stats';
import 'normalize.css';
import 'src/scss/index.scss'; /* eslint-disable-line */

(function() {

    // dynamically inject sidebar
    fetch('/sidebar/')
        .then((response) => {
            return response.text();
        })
        .then((html) => {
            const parser = new DOMParser();

            const doc = parser.parseFromString(html, 'text/html'); // shadow node
            
            const elem = document.querySelector('#main');
            elem.appendChild(doc.querySelector('#sidebar'));
        });

    window.addEventListener("click", function (event) {
        const eventTarget = event.target as HTMLDivElement;

        const isSearchLinkSelected = eventTarget.classList.contains('search-link')
            || eventTarget.id === 'search';

        if (Menu.isLocalePickerVisible) {
            Menu.toggleLocaleDropdown();
        }

        if (Menu.isSearchFormVisible && !isSearchLinkSelected) {
            Menu.toggleSearchForm();
        }
    });

    Menu.init();

    if (window.location.pathname.indexOf('/stats/') > -1) {
        Stats.init();
    }

    if (window.location.pathname.indexOf('/search/') > -1) {
        require('./search/index');
    }
}());
